class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 1,
      participantid: Date.now(),
      q1: "",
      q2: "",
      q3: "",
      q4: "",
      q5: "",
      q6: "",
      q7: "",
      currentTime: 0.0,
      condition: (Math.random() > 0.5)
    }
  }

  nextPage = () => (event) => {
    
    if (this.state.currentTime < 170 && this.state.page > 1) {
      return
    }
    console.log("condition",this.state.condition)

  	var new_page = Math.floor(this.state.page) + 1;
    if (this.state.condition){
      //then we switch 2 and 3
      if (this.state.page == 1) {
        new_page = 3
      } else if (this.state.page == 3) {
        new_page = 2
      } else if (this.state.page == 2) {
        new_page = 4
      }
    }
    console.log(new_page)
    if (new_page == 5) {
      const thiz = this;
      var q1 = thiz.state.q1;
      var q2 = thiz.state.q2;
      var q3 = thiz.state.q3;
      var q4 = thiz.state.q4;
      var q5 = thiz.state.q5;
      var q6 = thiz.state.q6;
      var q7 = thiz.state.q7;
      var d = new Date();
      var participantid = d.getTime();
      participantid = participantid%1000000
      var condition = thiz.state.condition
      if (q1.length > 0){
        if (!condition){
        //!condition means that the first video was the aroll-only video
          q1 = 5 - parseInt(q1);
        } else {
          q1 = parseInt(q1);
        }
      } else {
        q1 = null
      }
      if (q2.length > 0){
        if (!condition){
        //!condition means that the first video was the aroll-only video
          q2 = 5 - parseInt(q2);
        } else {
          q2 = parseInt(q2);
        }
      } else {
        q2 = null
      }
      if (q3.length > 0){
        if (!condition){
        //!condition means that the first video was the aroll-only video
          q3 = 5 - parseInt(q3);
        } else {
          q3 = parseInt(q3);
        }
      } else {
        q3 = null
      }
      if (q4.length > 0){
        if (!condition){
        //!condition means that the first video was the aroll-only video
          q4 = 5 - parseInt(q4);
        } else {
          q4 = parseInt(q4);
        }
      } else {
        q4 = null
      }
      if (q5.length > 0) {
        q5 = parseInt(q5);
      } else {
        q5 = null
      }
      if (q6.length > 0) {
        q6 = parseInt(q6);
      } else {
        q6 = null
      }
      if (q7.length > 0) {
        q7 = parseInt(q7);
      } else {
        q7 = null
      }
      var cond = 0
      if (condition) {
        cond = 1
      }
      var videotype = "dog_tato"
      const apiURL = 'https://dapptalk.org/videoquality.php';
      const formData = {
        q1: q1,
        q2: q2,
        q3: q3,
        q4: q4,
        q5: q5,
        q6: q6,
        q7: q7,
        participantid: participantid,
        condition: cond,
        videotype: videotype
      };
      console.log(formData)
      fetch(apiURL, {
        method: 'POST',
        mode: 'no-cors',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin':'*',
        },
        body: JSON.stringify({
          formData,
        })
      }).then((response) => response.text())
        .then((responseData) => { console.log("response: ");console.log(responseData); })
        .catch((err) => { console.log(err); });
      }
    this.setState({page: new_page})
  }

  handleClickQ1 = () => (event) => {
    this.setState({q1: event.target.attributes.getNamedItem('data-key').value})
  }

  handleClickQ2 = () => (event) => {
    this.setState({q2: event.target.attributes.getNamedItem('data-key').value})
  }

  handleClickQ3 = () => (event) => {
    this.setState({q3: event.target.attributes.getNamedItem('data-key').value})
  }

  handleClickQ4 = () => (event) => {
    this.setState({q4: event.target.attributes.getNamedItem('data-key').value})
  }

  handleClickQ5 = () => (event) => {
    this.setState({q5: event.target.attributes.getNamedItem('data-key').value})
  }

  handleClickQ6 = () => (event) => {
    this.setState({q6: event.target.attributes.getNamedItem('data-key').value})
  }

  handleClickQ7 = () => (event) => {
    this.setState({q7: event.target.attributes.getNamedItem('data-key').value})
  }


  arollPlay = () => (event) => {
    if (document.getElementById("aroll").paused) {
      document.getElementById("aroll").play();
    } else {
      document.getElementById("aroll").pause();
    }
  }

  callingUpdate = () => (event) => {
    this.setState({currentTime: event.target.currentTime})
  }

  render() {
  	if (this.state.page === 1) {
    return (
      <div className="App">
        <div className="App-intro top-buffer ">
        	<div>
    				Thank you very much for considering to contribute to our work.<br/>
            To complete this task, you will view two videos (3 minutes each) and then rate them.<br/>
            We will show you two videos of a narrator talking about a certain topic. In some parts of the videos there will be additional, explainatory video footage. <br/>
            The full task should not take longer than 10 minutes.
    			</div>
        </div>
        <button onClick={this.nextPage()} className="btn btn-primary">In understand</button>
      </div>
    );
	} else if (this.state.page === 2) {
	    return (
	      <div className="App">
	        <div className="App-intro top-buffer bottom-buffer">
	        	<div></div>
            <video width="720" id="aroll" key="43" height="320" onTimeUpdate={this.callingUpdate()}>
              <source src="./aroll.mp4" type="video/mp4"/>
              Your browser does not support the video tag.
            </video>
            <div>{Math.floor(this.state.currentTime/60)}:{Math.floor(this.state.currentTime%60)} / 3:03</div>
            <button type="button" onClick={this.arollPlay()} className="btn btn-secondary">Play / Pause</button>
	        </div>
        	<button onClick={this.nextPage()} className="btn btn-primary">Next</button>
	      </div>
	    );
	} else if (this.state.page === 2.5) {
    return (
      <div className="App">
        <div className="App-intro top-buffer ">
          <div>
            Please fill out this questionnaire. <b>Please first click 'submit' in the questionnaire and then click 'Next'</b>.
          </div>
          <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSc0frxt7X41Sgtp4XDCzyFq-4mQQf99zr-a1CQQSn-bkf82xA/viewform?embedded=true" width="700" height="1300" frameborder="0" marginheight="0" marginwidth="0">Loading...</iframe>
        </div>
        <button onClick={this.nextPage()} className="btn btn-primary">Next</button>
      </div>
      );
  } else if (this.state.page === 3) {
      return (
        <div className="App">
          <div className="App-intro top-buffer bottom-buffer">
            <div></div>
            <video width="720" id="aroll" key="12" height="320" onTimeUpdate={this.callingUpdate()}>
              <source src="./aroll_final.mp4" type="video/mp4"/>
              Your browser does not support the video tag.
            </video>
            <div>{Math.floor(this.state.currentTime/60)}:{Math.floor(this.state.currentTime%60)} / 3:03</div>
            <button type="button" onClick={this.arollPlay()} className="btn btn-secondary">Play / Pause</button>
          </div>
          <button onClick={this.nextPage()} className="btn btn-primary">Next</button>
        </div>
      );
  } else if (this.state.page === 3.5) {
    return (
      <div className="App">
        <div className="App-intro top-buffer ">
          <div>
            Please fill out this questionnaire. <b>Please first click 'submit' in the questionnaire and then click 'Next'</b>.
          </div>
          <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSc0frxt7X41Sgtp4XDCzyFq-4mQQf99zr-a1CQQSn-bkf82xA/viewform?embedded=true" width="700" height="1300" frameborder="0" marginheight="0" marginwidth="0">Loading...</iframe>
        </div>
        <button onClick={this.nextPage()} className="btn btn-primary">Next</button>
      </div>
      );
  } else if (this.state.page === 4) {
      return (
        <div className="App">
          <div className="App-intro top-buffer bottom-buffer">
            <div>Please answer the following questions about the two videos you just watched:</div>
            <QuestionnaireView context={this}/>
          </div>
          <button onClick={this.nextPage()} className="btn btn-primary">Submit</button>
        </div>
      );
  } else if (this.state.page === 5) {
    return (
        <div className="App">
          <div className="App-intro">
              <div>Thank you so much for your participation.</div>
              <div>Your MTurk code is 09234234.</div>
              <div>You can close this window now. Thanks!</div>
          </div>
        </div>
      );
  }
  }
}

export default App;



